<template>
  <article>
    <h4>1 ÜLDSÄTTED</h4>
    <p>
      1.1 Mittetulundusühingu (edaspidi “Ühing”) nimeks on mittetulundusühing
      Lapikud (lühendatult MTÜ Lapikud).<br />
      1.1.1 Ühingu mitteametlik nimi on Tallinna Tehnikaülikooli
      Tarkvaraarenduse klubi.<br />
      1.2 Ühingu asukohaks on Eesti Vabariik, Tallinn.<br />
      1.3 Ühing on mittetulundusühinguna eraõiguslik juriidiline isik, mis oma
      tegevuses juhindub Eesti Vabariigi seadustest, käesolevast põhikirjast ja
      muudest õigusaktidest.<br />
      1.4 Ühingu missiooniks on tegeleda tudengiteadusega infotehnoloogia
      valdkonnas Tallinna Tehnikaülikoolis.<br />
      1.4.1 Ühingu ülesanneteks on:
    </p>
    <ul>
      <li>
        Tallinna Tehnikaülikooli infotehnoloogiast huvitatud üliõpilaste
        koondamine ja rakendamine tarkvaraarenduse projektidesse.
      </li>
      <li>Info- ja telekommunikatsiooni alase õppetöö nõustamine.</li>
    </ul>
    <p>1.4.2 Ühingu tegevuse põhimõteteks on:</p>
    <ul>
      <li>liikmete hääleõiguse võrdsus;</li>
      <li>liikmelisuse vabatahtlikkus ja mitteüleantavus;</li>
      <li>liikmete, ainult põhikirjast tulenevad, kohustused Ühingu ees;</li>
      <li>liikmete õigus Ühingust välja astuda;</li>
      <li>liikmete vahel mittejaotatava vara, tulu ja kapitali olemasolu.</li>
    </ul>
    <p>
      1.4.3 Ühingu põhitegevuseks ei ole majandustegevuse kaudu tulu saamine.<br />
      1.5 Ühing võib omada varalisi ja mittevaralisi õigusi ning võtta
      kohustusi, tal on oma vara, iseseisev bilanss ning oma sümboolika.<br />
      1.6 Ühing vastutab oma kohustuste täitmise eest kogu oma varaga. Ühingu
      liige vastutab Ühingu kohustuste täitmise eest vaid tasutud liikmemaksu
      (sisseastumismaksu ja aastamaksude) ulatuses.<br />
      1.7 Ühing on asutatud määramata ajaks.<br />
      1.8 Ühingu majandusaasta algus on 1. jaanuaril ja lõpp 31. detsembril.<br />
      1.9 Ühingu töökeeleks on eesti keel.
    </p>
    <h4>2 LIIKMELISUS. LIIKMETE ÕIGUSED JA KOHUSTUSED</h4>
    <p>
      2.1 Kui käesolevas põhikirjas ei ole sätestatud teisiti, võib Ühingu
      liikmeks astuda iga füüsiline isik, kes on õppinud või õpib Tallinna
      Tehnikaülikoolis. Liikmeks vastuvõtu otsustab juhatus 1 kuu jooksul alates
      avalduse esitamisest. Juhatus peab liikmete registrit.<br />
      2.2 Üldkoosolek võib erandkorras ja lihthäälteenamusega ühingu liikmeks
      vastu võtta ka punkti 2.1 nõudmistele mittevastavaid isikuid.<br />
      2.3 Liikmetel on õigus:
    </p>
    <ul>
      <li>osaleda Ühingu koosolekutel;</li>
      <li>valida Ühingu juhatust ning revisjonikomisjoni;</li>
      <li>kandideerida Ühingu juhatusse ja/või revisjonikomisjoni;</li>
      <li>saada teavet ühingu tegevuse tulemuste kohta;</li>
      <li>astuda ühingust välja;</li>
      <li>
        osaleda juhatuse koosolekul juhul, kui koosolekut ei ole kuulutatud
        kinniseks;
      </li>
      <li>teha ettepanekuid Ühingu tegevuse tõhustamiseks;</li>
      <li>
        osaleda Ühingu poolt informaatikatudengitele korraldatavatel üritustel;
      </li>
      <li>
        algatada Ühingu põhikirja muutmist vähemalt ühe kümnendiku Ühingu
        liikmete ühisavaldusega.
      </li>
      <li>
        Juhatus rahuldab väljaastumise avalduse 1 kuu jooksul avalduse esitamise
        päevast /arvates/ alates. Kui liikmelisus lõpeb majandusaasta kestel,
        siis aastamaksu ei tagastata ning selle aasta eest kuulub liikmemaks
        tasumisele.
      </li>
    </ul>
    <p>
      2.4 Kui ühingu juhatus ei ole mingil põhjusel võimeline sisseastumis- või
      lahkumisavaldust ettenähtud aja jooksul rahuldama, otsustab liikme sisse-
      või väljaastumise üldkoosolek.<br />
      2.5 Ühingu auliikmeks võib olla iga füüsiline või juriidiline isik.<br />
      2.6 Ühingu auliikmeks vastu võtmise või auliikme staatuse tühistamise
      otsustab üldkoosolek.<br />
      2.7 Ühingu auliige ei ole hääleõiguslik ega või olla valitud juhatuse ega
      revisjonikomisjoni liikmeks.<br />
      2.8 Ühingu liikmelisus on seotud sisseastumismaksuga ja aastamaksuga.
      Liikmed tasuvad sisseastumismaksu ja aastamaksu ühingu pangaarvele
      üldkoosoleku poolt kindlaksmääratud suuruses ja tähtajaks.<br />
      2.9 Ühingu auliikmele sisseastumismaksu ja aastamaksu ei rakendata.<br />
      2.10 Ühingu liige võidakse ühingust välja arvata:
    </p>
    <ul>
      <li>kui liige ei täida põhikirjaga sätestatud kohustusi;</li>
      <li>liikme surma puhul;</li>
      <li>kui liige on saanud hakkama vääritu teoga.</li>
      <li>Liikme ühingust välja arvamise otsustab juhatus.</li>
    </ul>
    <p>
      2.11 Juhatuse otsuse liikme väljaarvamise kohta võib liige edasi kaevata
      ühingu üldkoosolekule seaduses ettenähtud korras ja nõuda enda
      väljaarvamise otsustamist üldkoosoleku poolt.<br />
      2.12 Isikul, kelle liikmelisus ühingus on lõppenud, ei ole õigusi ühingu
      vara suhtes.<br />
      2.13 Ühingu liige on kohustatud:
    </p>
    <ul>
      <li>
        täitma põhikirja järgseid nõudeid ja ühingu juhtorganite seaduspäraseid
        otsuseid;
      </li>
      <li>õigeaegselt tasuma aastamaksu;</li>
      <li>mitte rikkuma ühingu mainet.</li>
    </ul>
    <h4>3 JUHTIMINE</h4>
    <p>
      3.1 Ühingu kõrgeimaks organiks on selle liikmete üldkoosolek, kus võivad
      osaleda kõik ühingu liikmed. Üldkoosolek tuleb kokku vähemalt 1 kord
      aastas.<br />
      3.2 Üldkoosolek võtab vastu otsuseid kõikides ühingu juhtimise küsimustes,
      mida ei ole seaduse või käesoleva põhikirjaga antud juhatuse või ühingu
      muu organi pädevusse.<br />
      3.3 Üldkoosoleku pädevusse kuulub:
    </p>
    <ul>
      <li>ühingu ühinemine ja jagunemine;</li>
      <li>põhikirja muutmine;</li>
      <li>eesmärkide muutmine;</li>
      <li>juhatuse liikmete määramine;</li>
      <li>juhatuse või selle liikmete tagasikutsumine;</li>
      <li>juhatuse pädevust ületades tehtud otsuse tühistamine;</li>
      <li>muude põhikirjaga ettenähtud organite liikmete valimine;</li>
      <li>aastaaruande ja bilansi ning revisjoniaruande kinnitamine;</li>
      <li>hinnangu andmine juhatuse tegevusele;</li>
      <li>
        juhatuse või muu organi liikmega tehingu tegemise või tema vastu nõude
        esitamise otsustamine ja selles tehingus või nõudes ühingu esindaja
        määramine;
      </li>
      <li>
        muude küsimuste otsustamine, mida ei ole seaduse või põhikirjaga antud
        ühingu teiste organite pädevusse.
      </li>
    </ul>
    <p>
      3.4 Üldkoosoleku otsused on avalikud ja avaldatakse Ühingu
      koduleheküljel.<br />
      3.5 Ühingu põhikirja või tegevuse eesmärkide muutmiseks on vajalik
      vähemalt 50% ühingu liikmete või nende esindajate osavõtt üldkoosolekul.
      Põhikirja või eesmärkide muutmise otsus on vastu võetud, kui selle poolt
      hääletavad üle 50% koosolekul osalenud liikmetest või nende
      lihtkirjalikult volitatud esindajatest.<br />
      3.6 Ühingu juhtorganiks on juhatus, kes üldkoosolekute vahelisel ajal
      esindab Ühingut ja kes juhib Ühingu põhikirjalist tegevust ja täidab
      käesoleva põhikirjaga talle pandud ülesandeid ning üldkoosoleku poolt
      delegeeritud õiguste ja kohustuste piires.<br />
      3.7 Üldkoosolek valib demokraatlikel valimistel salajasel hääletamisel
      kolme- kuni viieliikmelise juhatuse. Ühingu juhatuse liikmeid saab valida
      Ühingu liikmete seast. Juhatuse volituse aja ning selleks ajaks valitavate
      juhatuse liikmete arvu otsustab üldkoosolek.<br />
      3.8 Juhatuse ülesanded:
    </p>
    <ul>
      <li>
        Ühingu esindamine nii Tallinna Tehnikaülikoolis kui väljaspool seda;
      </li>
      <li>Ühingu liikmete informeerimine oma tegevusest;</li>
      <li>Ühingu liikmete info haldamine;</li>
      <li>valmistada ette ja kuulutada välja üldkoosolekuid;</li>
      <li>korraldada Ühingu käsutuses oleva vara valdamist ja kasutamist;</li>
      <li>
        korraldada Ühingu rahaliste vahendite käsutamist üldkoosoleku poolt
        kehtestatud korras;
      </li>
      <li>
        korraldada juhatuse koosolekuid vähemalt 2 korda poolaasta jooksul;
      </li>
      <li>muud Ühingu eesmärkidega kooskõlas olevad ülesanded.</li>
    </ul>
    <p>
      3.9 Üldkoosoleku otsusega võib poolte kokkuleppel Ühingu juhatusele anda
      täiendavaid ülesandeid.<br />
      3.10 Juhatus teatab oma otsustest liikmetele, kas suuliselt või
      kirjalikult, ühe nädala jooksul.<br />
      3.11 Juhatus on aruandekohustuslik üldkoosoleku ees.<br />
      3.12 Juhatuse otsused võetakse vastu juhatuse koosolekul lihthäälteenamuse
      alusel. Juhatuse koosolek on otsustusvõimeline, kui kohal on vähemalt 2/3
      juhatuse liikmetest.<br />
      3.13 Juhatuse liikmetel on õigus saada tagasivalituks.<br />
      3.14 Juhatus korraldab ühingu raamatupidamist vastavalt raamatupidamise
      seadusele ning omab aruandekohustust üldkoosoleku ees vastavalt
      seadusele.<br />
      3.15 Juhatuse igal liikmel on õigus esindada ühingut kõikides
      õigustoimingutes eraldi.<br />
      3.16 Juhatuse liikme tagasi kutsumiseks on vajalik vähemalt 50% ühingu
      liikmete või nende esindajate osavõtt üldkoosolekul. Tagasikutsumise
      otsustab üldkoosolek salajasel hääletusel. Juhatuse liikme tagasi
      kutsumise otsus on vastu võetud, kui selle poolt hääletavad üle 50%
      koosolekul osalenud liikmetest või nende esindajatest.<br />
      3.17 Tagasikutsumist võib algatada, kui seda nõuavad vähemalt 10% Ühingu
      liikmetest.<br />
      3.18 Tagasikutsumine tuleb vormistada kirjalikult ning esitada Ühingu
      juhatusele. Tagasikutsumisavalduse peab päevakorda võtma esitamisele
      järgneval üldkoosolekul esimese päevakorrapunktina.<br />
      3.19 Juhatuse liikme tagasiastumise või ühingust välja arvamise korral
      valib üldkoosolek uue juhatuse liikme järgmisel korralisel või
      erakorralisel üldkoosolekul.
    </p>
    <h4>4 ÜHINGU TÖÖKORD</h4>
    <p>
      4.1 Ühingu töö vormiks on koosolek.<br />
      4.2 Koosoleku kutsub kokku Ühingu juhatus või projektijuht.<br />
      4.3 Korralisest üldkoosolekust tuleb juhatusel Ühingu liikmeid teavitada
      vähemalt 3 nädalat enne üldkoosoleku toimumist ning erakorralisest
      üldkoosolekust vähemalt 1 nädal enne. Teavitamine käib läbi Ühingu
      kodulehekülje, meililisti või e-posti teel.<br />
      4.4 Koosoleku kokkukutsujal on õigus koosolek kuulutada kinniseks. Kinnise
      koosoleku ajal võivad koosoleku ruumis viibida vaid Ühingu liikmed ning
      koosoleku kokkukutsujalt selleks loa saanud inimesed.<br />
      4.5 Üldkoosolekuid juhatab üks juhatuse liikmeist.<br />
      4.6 Juhatus võib soovi korral valida üldkoosolekut juhatama ka teisi
      Ühingu liikmeid.<br />
      4.7 Igal Ühingu liikmel on üks hääl. Üldkoosolek on otsustusvõimeline, kui
      sellel osaleb või on lihtkirjalike volituste alusel esindatud vähemalt 30%
      ühingu liikmeist. Otsused võetakse vastu lihthäälteenamuse alusel, kui
      konkreetse juhu kohta pole põhikirjas sätestatud teisiti. Kui üldkoosolek
      ei ole ülalsätestatud kvooruminõude tõttu pädev otsuseid vastu võtma,
      kutsub juhatus ühe nädala jooksul kokku uue üldkoosoleku sama
      päevakorraga. Uus üldkoosolek on pädev otsuseid vastu võtma, sõltumata
      üldkoosolekul osalenud või esindatud liikmete arvust, kuid üksnes juhul,
      kui üldkoosolekul osaleb või on esindatud vähemalt 7 liiget.<br />
      4.8 Hääletada saab otsuse poolt, vastu või jääda erapooletuks.<br />
      4.9 Hääletamine Ühingu koosolekul on avalik, kui käesolevas põhikirjas ei
      ole ette nähtud teisiti.<br />
      4.10 Kõik koosolekul toimuvad isikuvalimised on salajased.<br />
      4.11 Koosoleku kokkukutsuja võib otsustada, et hääletus on salajane, kui
      seda nõuab vähemalt 5 Ühingu liiget.<br />
      4.12 Kõiki Ühingu koosolekuid protokollitakse ja vastuvõetud otsused
      vormistatakse eraldi aktina.<br />
      4.13 Ühingu koosoleku protokoll ja otsused peavad olema tehtud
      kättesaadavaks kümne tööpäeva jooksul pärast koosoleku toimumist.
    </p>
    <h4>5 MAJANDUSTEGEVUS JA VARA JAOTUS</h4>
    <p>
      5.1 Ühingu vara tekib liikmete sisseastumismaksudest, aastamaksudest ja
      muudest maksetest, ühingu vara kasutamisest ja tegevusest saadavast tulust
      ning muudest laekumistest või annetustest.<br />
      5.2 Ühing ekspluateerib ja remondib tema valduses või omandis olevat vara
      täieliku isemajandamise põhimõttel ja lepinguvabaduse tingimustes.<br />
      5.3 Vara valdamist, kasutamist ja säilimist ning rahaliste vahendite
      käsutamist korraldab Ühingu juhatus põhikirjaliste ülesannete täitmiseks
      kooskõlas õigusaktide ja kehtestatud korraga.
    </p>
    <h4>6 TEADETE AVALIKUSTAMINE</h4>
    <p>
      6.1 Juhatuse, revisjonikomisjoni ja muude ühingu organite teated ühingu
      liikmetele avalikustatakse üldkoosolekul.<br />
      6.2 Edasilükkamatuid ja kiireloomulisi teateid võidakse avalikustada
      liikmeile ka globaalsete, üleriigiliste või kohalike meediakanalite kaudu.
    </p>
    <h4>7 KONTROLL JA REVIDEERIMINE</h4>
    <p>
      7.1 Üldkoosolek teostab järelvalvet teiste ühingu organite tegevuse üle.
      Selle ülesande täitmiseks võib määrata üldkoosolek revisjoni või
      audiitorkontrolli. Revisjoni volituste aja ning selleks ajaks valitavate
      revisjonikomisjoni liikmete arvu otsustab üldkoosolek.<br />
      7.2 Revisjonikomisjoni liige ei pea olema Ühingu liige.<br />
      7.3 Revisjonikomisjoni liige ei tohi kuuluda juhatusse.<br />
      7.4 Revisjonikomisjon on aruandekohustuslik üldkoosoleku ees.<br />
      7.5 Juhatuse ja muu organi liikmed peavad võimaldama revidendil
      (revisjonikomisjonil) või audiitoril tutvuda kõigi revisjoni või
      audiitorkontrolli läbiviimiseks vajalike dokumentidega ning andma
      vajalikku teavet.
    </p>
    <h4>8 LÕPETAMINE, ÜHINEMINE JA JAGUNEMINE</h4>
    <p>
      8.1 Ühingu lõpetamine, ühinemine ja jagunemine toimub seaduses sätestatud
      korras.<br />
      8.2 Ühingu lõpetamise, ühinemise ja jagunemise otsustamiseks on vajalik
      vähemalt 2/3 ühingu liikmete või nende esindajate osavõtt üldkoosolekul.
      Ühingu lõpetamise, ühinemise või jagunemise otsus on vastu võetud, kui
      selle poolt hääletavad üle 2/3 koosolekul osalenud liikmetest või nende
      esindajatest.<br />
      8.3 Ühingu likvideerijaks on juhatus.<br />
      8.4 Pärast võlausaldajate kõigi nõuete rahuldamist ja nõuet mitte esitanud
      teada olevate võlausaldajate raha deponeerimist jaotatakse ülejäänud vara
      üldkoosoleku otsusega määratud õigustatud isikute vahel.<br />
      Käesolev põhikiri on vastu võetud mittetulundusühingu Lapikud
      asutamiskoosolekul 23. veebruaril 2002. a. ühehäälselt ning täiendatud
      üldkoosolekul 20. mail 2004. a ja 4.mail 2018 a.
    </p>
  </article>
